const switchBtn = document.querySelector(".header__theme__switch");

switchBtn.addEventListener("click", () => {
  document.querySelector("html")?.classList.toggle("lightModeBackground");
  document.querySelector("html")?.classList.toggle("lightModeText");

  const list = document.querySelectorAll(".countryItem, input, select");
  list.forEach((element) => {
    element.classList.toggle("lightModeElements");
  });
});
